<template>
  <div>
    <banner :link="true" @link-exec="link" v-if="isMemberProcess">
      編輯訂閱項目
    </banner>
    <banner v-else>
      訂閱考試資訊內容
    </banner>
    <div class="main-content" style="display: flex; flex-direction: column">
      <div style="padding: 0 16px;">
        <p class="font-14-normal">請至少選擇 3 項</p>
        <subscribe-label-map :list="tempList" v-model="listOne"></subscribe-label-map>
      </div>
    </div>
    <base-footer :class="[listOne.length >= 3 ? '' : 'disabled']" @next-exec="next">
      {{isMemberProcess ? '儲存' : '下一步'}}
    </base-footer>
  </div>
</template>

<script>
import { Component, Vue } from 'vue-property-decorator';
import Banner from '../../components/Banner.vue';
import BaseFooter from '../../components/BaseFooter.vue';
import SubscribeLabelMap from '../../components/SubscribeLabelMap.vue';

@Component({
  components: {
    Banner,
    BaseFooter,
    SubscribeLabelMap,
  },
})

export default class EditSubscribeItem extends Vue {
  tempList = ['test1', 'test2', 'test3'];
  listOne = ['test1'];

  get isMemberProcess() {
    return this.$route.query.process === 'member';
  }

  link() {
    this.$router.push('/member');
  }

  next() {
    if (this.isMemberProcess) console.log(this.listOne);
    else this.$router.push('/member');
  }
}
</script>

<style lang="scss" scoped>
div .main-content {
  box-sizing: border-box
}
</style>
